.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 10vh;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

.test {

  
  display: grid;
  place-items: center;
  min-width: 100vh;
  min-height: 100vh;

  
}

.inner {
  background-color: #61dafb;
  width: 100px;
  height: 100px;

}

.inner:hover {
  animation: button-transform infinite 0.1s linear alternate;
}

@keyframes big-spin {
  from {
    transform: rotate3d(0,0,1,0deg) scale(2);
  }
  to {
    transform: rotate3d(0,0,1,360deg) scale(0);
  }

}


@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

@keyframes button-transform {
  from {
    transform: scale(1) rotate(5deg);
  }
  to {
    transform: scale(1.02) rotate(-5deg);
  }
}
